jQuery.noConflict();

document.addEventListener( 'DOMContentLoaded', () => {

	const disconnectSquareBtn = document.getElementById( 'give-square-disconnect' );
	const donationStatus = document.getElementById( 'give-payment-status' );
	const squareStyles = document.getElementById( 'square_styles' );

	// Show modal popup with relevant message on disconnect square button.
	if ( null !== disconnectSquareBtn ) {
		disconnectSquareBtn.addEventListener( 'click', ( e ) => {

			// Restrict redirect temporarily until user input.
			e.preventDefault();

			const disconnectPopupText = disconnectSquareBtn.getAttribute( 'data-disconnect-popup-text' );

			// Show modal popup for confirmation.
			new Give.modal.GiveConfirmModal({
				classes: {
					modalWrapper: 'give-modal--warning'
				},
				modalContent: {
					title: giveSquareAdminLocaliseVars.disconnect_square_title,
					desc: disconnectPopupText
				},
				successConfirm: () => {
					const disconnectSquare = new XMLHttpRequest();
					const formData = new FormData();

					formData.append( 'action', 'square_disconnect_connection' );
					formData.append( 'security', giveSquareAdminLocaliseVars.ajaxNonce );

					// Do something on Ajax on state change.
					disconnectSquare.onreadystatechange = function( evt ) {
						if (
							4 === this.readyState &&
							200 === this.status &&
							'success' !== this.responseText
						) {
							window.location.href = e.target.getAttribute( 'href' );
						}
					};
					disconnectSquare.open( 'POST', ajaxurl, false );
					disconnectSquare.send( formData );
				}
			}).render();
		});
	}

	if ( null !== donationStatus ) {
		donationStatus.addEventListener( 'change', ( event ) => {

			const squareCheckbox = document.getElementById( 'give-square-opt-refund' );

			if ( null === squareCheckbox ) {
				return;
			}

			squareCheckbox.checked = false;

			// If donation status is complete, then show refund checkbox
			if ( 'refunded' === event.target.value ) {
				document.getElementById( 'give-square-opt-refund-wrap' ).style.display = 'block';
			} else {
				document.getElementById( 'give-square-opt-refund-wrap' ).style.display = 'none';
			}
		});
	}

	squareJsonFormattedTextarea( squareStyles );
});

/**
 * This function will help to beautify JSON data.
 *
 * @param element
 * @param value
 *
 * @since 1.0.0
 */
function squarePrettyJson( element, value ) {
	let jsonData = '';
	const saveButton = document.querySelector( '.give-save-button' );

	try {
		jsonData = JSON.parse( value );
		element.value = JSON.stringify( jsonData, undefined, 2 );
		element.style.border = 'none';
		saveButton.removeAttribute( 'disabled' );
	} catch ( e ) {
		element.style.border = '1px solid red';
		saveButton.setAttribute( 'disabled', 'disabled' );
	}
}

/**
 * This will trigger textarea to validate json formatted input.
 *
 * @param element
 *
 * @since 1.0.0
 */
function squareJsonFormattedTextarea( element ) {
	if ( null !== element ) {

		squarePrettyJson( element, element.value );

		element.addEventListener( 'blur', ( event ) => {
			squarePrettyJson( element, event.target.value );
		});
	}
}



